const actions = {
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  }),
  getStockValuationReports: (page, search, filter, filter_select) => ({
    type: "GET_STOCK_VALUATION_REPORTS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter,
      filter_select,
    }
  }),
  getItemsForStockValuationReport: (id_location, search) => ({
    type: "GET_ITEMS_FOR_STOCK_VALUATION_REPORT_REQUEST",
    payload: { id_location, search }
  })
};

export default actions;
