import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Link } from "react-router-dom";
import moment from "moment";

import notification from "../../../../components/notification";
import {
  Col,
  Collapse,
  DatePicker,
  Divider,
  Empty,
  Row,
  Spin,
  Select,
  Tooltip
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import NumberFormat from "../../../App/NumberFormat";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/stockValuationDetailReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  // StatusTag,
  customCollapseStyle
} from "../../../App/app.style";

import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
// const Search = Input.Search;
// const { Column, ColumnGroup } = Table;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";

class StockValuationDetailReport extends Component {
  state = {
    filter: {
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      id_item: null,
      id_location: null,
      sort_by: "transaction_date",
      sort_type: "asc"
    },
    //firstFilter: true,
    search: null,
    search_item: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getStockValuationReports } = this.props;
    getStockValuationReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.stockValuationDetailReport.error) {
      notification("error", this.props.stockValuationDetailReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.stockValuationDetailReport.success) {
      notification("success", this.props.stockValuationDetailReport.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getStockValuationReports } = this.props;
    getStockValuationReports(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getStockValuationReports } = this.props;
    this.setState({
      filter: {
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        id_item: null,
        id_location: null,
        sort_by: "transaction_date",
        sort_type: "asc"
      }
    });
    this.setState({ search: null });
    getStockValuationReports();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    // const { setFieldsValue } = this.props.form;
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else if (name === "id_location") {
      const { getItemsForStockValuationReport } = this.props;
      getItemsForStockValuationReport(value);
      this.setState({
        filter: {
          ...this.state.filter,
          id_item: null,
          id_location: value
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.id_item) {
      string
        ? (string += "&id_item=" + filter.id_item)
        : (string += "id_item=" + filter.id_item);
    }

    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getStockValuationReports } = this.props;

    let filter = this.filterValue();

    this.setState({ search: null });
    getStockValuationReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getStockValuationReports } = this.props;
    this.setState({
      filter: {
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        id_item: null,
        id_location: null,
        sort_by: "transaction_date",
        sort_type: "asc"
      }
    });
    this.setState({ search: null });
    getStockValuationReports();
  };

  handleSearchItem = value => {
    const { getItemsForStockValuationReport } = this.props;
    const { id_location } = this.state.filter;
    this.setState({ search_item: value });
    if (id_location) {
      getItemsForStockValuationReport(id_location, value);
    }
  };

  render() {
    // Definisi kolom2 serta field2 API yang dipakai tabel
    let columns = [
      {
        title: getLanguage({ id: "transactionDate" }),
        date: true,
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        render: (text, row) => (
          <span>{moment(text).format("DD MMMM YYYY  HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        rowKey: "item_name"
      },
      {
        title: getLanguage({ id: "description" }),
        dataIndex: "description",
        rowKey: "description",
        width: "22%",
        render: text => {
          if (text.length > 75) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 75) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      // {
      //   title: "Location",
      //   dataIndex: "location_name",
      //   rowKey: "location_name"
      // },
      {
        title: getLanguage({ id: "totalIn" }),
        align: "center",
        children: [
          {
            title: getLanguage({ id: "qty" }),
            dataIndex: "qty_in",
            rowKey: "qty_in",
            align: "right",
            parse: true,
            render: (text, row) => {
              if (row.qty_change && row.qty_change.slice(0, 1) !== "-") {
                return <NumberFormat value={parseFloat(row.qty_change)} />;
              } else return "";
            }
          },
          {
            title: getLanguage({ id: "value" }),
            dataIndex: "total_in",
            rowKey: "total_in",
            align: "right",
            parse: true,
            render: (text, row) => {
              if (row.total_cost && row.qty_change.slice(0, 1) !== "-") {
                return <NumberFormat value={parseFloat(row.total_cost)} />;
              } else return "";
            }
          }
        ]
      },
      {
        title: getLanguage({ id: "totalOut" }),
        align: "center",
        children: [
          {
            title: getLanguage({ id: "qty" }),
            dataIndex: "qty_out",
            rowKey: "qty_out",
            align: "right",
            parse: true,
            render: (text, row) => {
              if (row.qty_change && row.qty_change.slice(0, 1) === "-") {
                return (
                  <NumberFormat value={parseFloat(Math.abs(row.qty_change))} />
                );
              } else return "";
            }
          },
          {
            title: getLanguage({ id: "value" }),
            dataIndex: "total_out",
            rowKey: "total_out",
            align: "right",
            parse: true,
            render: (text, row) => {
              if (row.total_cost && row.qty_change.slice(0, 1) === "-") {
                return (
                  <NumberFormat value={parseFloat(Math.abs(row.total_cost))} />
                );
              } else return "";
            }
          }
        ]
      },
      {
        title: getLanguage({ id: "balance" }),
        align: "center",
        children: [
          {
            title: getLanguage({ id: "qty" }),
            dataIndex: "qty_balance",
            rowKey: "qty_balance",
            align: "right",
            parse: true,
            render: text => {
              return <NumberFormat value={parseFloat(text)} />;
            }
          },
          {
            title: getLanguage({ id: "value" }),
            dataIndex: "value_balance",
            rowKey: "value_balance",
            align: "right",
            parse: true,
            render: (text, row) => {
              if (text) {
                return <NumberFormat value={parseFloat(text)} />;
              } else return 0;
            }
          }
        ]
      }
    ];

    // Data setup
    const { stockValuationDetailReport } = this.props;
    const {
      items,
      locations,
      reports,
      filter,
      filter_select
    } = stockValuationDetailReport;
    //console.log(this.props.stockValuationDetailReport); //console.log(this.state);
    const dataSource = [];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    // let total_amount = 0;
    // let total_discount = 0;
    // let total_qty = 0;
    // let total_tax = 0;
    const {
      loading,
      loadingForeign,
      loadingItem
    } = this.props.stockValuationDetailReport;

    // let pdfColumns = columns.map(data => {
    //   if (data.title === getLanguage({id: "description"})) {
    //     data.render = function (text) {
    //       return text;
    //     };
    //   }
    //   return data;
    // });

    if (reports) {
      // var item_cost_balance = 0;
      let qty_balance = 0;

      reports.map((report, key) => {
        let qty_in = null;
        let total_in = null;
        let qty_out = null;
        let total_out = null;

        report.qty_change
          ? (qty_balance += parseFloat(report.qty_change))
          : (qty_balance += parseFloat(report.qty_balance));
        report.qty_balance = parseFloat(qty_balance);

        if (report.qty_change.slice(0, 1) === "-" && report.total_cost) {
          qty_out = parseFloat(Math.abs(report.qty_change));
          total_out = report.total_cost
            ? parseFloat(Math.abs(report.total_cost))
            : 0;
        } else {
          qty_in = report.qty_change;
          total_in = report.total_cost ? report.total_cost : 0;
        }

        dataSource.push({
          ...report,
          qty_in,
          total_in,
          qty_out,
          total_out,
          key
        });
      });
    }

    const fileName = getLanguage({ id: "stockValuationDetailReports.pageHeader" });

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="stockValuationDetailReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={
                <IntlMessages id="stockValuationDetailReports.pageHeader" />
              }
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
              onAfterPrint={() => (document.title = siteConfig.documentTitle)}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              scale={0.4}
              title="stockValuationDetailReports.pageHeader"
            />

            <ExcelWrapper
              title="stockValuationDetailReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />
            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      // defaultValue="all"
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations && locations.length > 0
                        ? locations.map((data, key) => (
                            <Option key={key} value={data.id_location}>
                              {`${data.location_code} (${data.location_name})`}
                            </Option>
                          ))
                        : null}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="sidebar.item" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(this, "id_item")}
                      //help={<IntlMessages id="required" />}
                      style={{ width: "100%" }}
                      placeholder={<IntlMessages id="selectItem" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingItem}
                      value={
                        this.state.filter.id_item
                          ? this.state.filter.id_item
                          : []
                      }
                      notFoundContent={
                        loadingItem ? (
                          <Spin size="small" />
                        ) : this.state.filter.id_location ? (
                          <Empty
                            description={
                              <IntlMessages id="items.table.noData" />
                            }
                          />
                        ) : (
                          <Empty
                            description={<IntlMessages id="noLocation" />}
                          />
                        )
                      }
                      onSearch={this.handleSearchItem}
                    >
                      {items.map((data, key) => {
                        if (this.state.filter.id_location) {
                          return (
                            <Option key={key} value={data.id_item}>
                              {`${data.item_code} (${data.item_name})`}
                            </Option>
                          );
                        }
                      })}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ alignItems: "flex-end", marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="salesOrders.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>

                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={9}>
                        <label>
                          <IntlMessages id="stockValuationReports.sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "transaction_date"
                          }
                          style={{ width: "95%" }}
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="transaction_date">
                            <IntlMessages id="purchaseOrderReports.filter.date" />
                          </Option>
                          {/* <Option key={2} value="item_name">
                            <IntlMessages id="purchaseOrders.item" />
                          </Option>
                          <Option key={3} value="location_name">
                            <IntlMessages id="sidebar.location" />
                          </Option>
                          <Option key={4} value="total_cost">
                            <IntlMessages id="totalCost" />
                          </Option>
                          <Option key={5} value="Quantity">
                            <IntlMessages id="quantity" />
                          </Option> */}
                        </Select>
                      </Col>
                      {/* <Col span={7}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "95%" }}
                          optionFilterProp="children"
                          filterOption={true}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col> */}
                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          disabled={!this.state.filter.id_item ? true : false}
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.submit" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="noItemLocation" />} />
            ) : !filter.includes("id_item=") ? (
              <Empty description={<IntlMessages id="noItems" />} />
            ) : dataSource.length === 0 &&
              !this.props.stockValuationDetailReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="stockValuationReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    columns={columns}
                    dataSource={dataSource}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    stockValuationDetailReport: state.stockValuationDetailReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  StockValuationDetailReport
);
